
	import { defineComponent } from 'vue';

	export default defineComponent({
		name: 'NewGameButton',
		props: {
			newPlayer: {
				type: Boolean,
				default: true,
			},
		},
		emits: ['start-new-game'],
		setup(props, { emit }) {
			const startNewGame = () => {
				emit('start-new-game');
			};

			return {
				startNewGame,
			};
		},
	});
